@mixin alert() {
    background: #eee;
    padding: 15px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    p {
        margin-bottom: 0;
    }
}
.my-alert {
    @include alert()
}
.my-alert2 {
    @extend .my-alert;
    background: #ffe591;
}
